import axios from "axios";
import Header from '../../modules/header.js';
import Footer from '../../modules/footer.js';
import React, { useState, useEffect} from 'react';
import {useNavigate} from 'react-router-dom';
import DatePicker from "react-datepicker";
import { useSelector } from 'react-redux';
import "react-datepicker/dist/react-datepicker.css";
import Box from '@mui/material/Box';
import LinearProgress from '@mui/material/LinearProgress';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';

export default function NewMedicalRecord() {
    const [hospital, setHospital] = useState("");
    const [doctor, setDoctor] = useState("");
    const [dateOfVisit, setDateOfVisit] = useState(new Date());
    const [date, setDate] = useState("");
    const [historyOfPresentIllness, setHistoryOfPresentIllness] = useState("");
    const [pastMedicalHistory, setPastMedicalHistory] = useState("");
    const [medications, setMedications] = useState("");
    const [allergies, setAllergies] = useState("");
    const [diagnosis, setDiagnosis] = useState("");
    const [treatment, setTreatment] = useState("");
    const [additionalComments, setAdditionalComments] = useState("");
    const [isLoading, setIsLoading] = useState(false);
    const navigate = useNavigate();
    const [doctorDid, setDoctorDid] = useState("");
    // eslint-disable-next-line
    const [name, setName] = useState(useSelector(state => state.patientName));
    // eslint-disable-next-line
    const [patientJwt, setPatientJwt] = useState(useSelector(state => state.patientJwt));

    useEffect(() => {  
        setDoctorDid(localStorage.getItem("did"));
    }, []);

    useEffect(() => {
        const temp = dateOfVisit.toString().split(" ");
        let month = temp[1];
        switch(month) {
            case "Jan": month="1"; break;
            case "Feb": month="2"; break;
            case "Mar": month="3"; break;
            case "Apr": month="4"; break;
            case "May": month="5"; break;
            case "Jun": month="6"; break;
            case "Jul": month="7"; break;
            case "Aug": month="8"; break;
            case "Sep": month="9"; break;
            case "Oct": month="10"; break;
            case "Nov": month="11"; break;
            case "Dec": month="12"; break;
            default : break;
        }
        let _date = temp[3]+"/"+month+"/"+temp[2];
        setDate(_date);
        _date = "";
    }, [dateOfVisit]);

    const handleClick = async () => {

        const serverIP = process.env.REACT_APP_SERVER_IP_ADDRESS;

        await axios.post(`https://${serverIP}:5001/user/new-record`, {
                doctorDID: doctorDid,
                patientDID: patientJwt,
                hospital: hospital,
                dn: doctor,
                dv: date,
                hi: historyOfPresentIllness,
                ph: pastMedicalHistory,
                me: medications,
                al: allergies,
                di: diagnosis,
                tr: treatment,
                ac: additionalComments,
            })
            .then(res => {
                setIsLoading(false);
                console.log(res);
                // localStorage.setItem("jwt", res.data.updatedVcJwt);  // ->   .
                navigate(`/patient-medical-records?patient=${name}`);
            }).catch(err => console.log(err))
    }

    return (
        <div className='root'>
            <Header />
            {
                isLoading ?
                <div className='column-center auth-loading'>
                    <p>  </p>
                    <Box sx={{ width: '60%' }}> 
                        <LinearProgress />
                    </Box>
                </div>
                :
                <div className='body column-center'>
                <p style={{ fontSize: '30px' }}> </p>
                <div className='input-container'>
                    <p className='input-title' 
                        style={{ marginBottom: '3vh' }}> </p>
    
                    {/*  */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'></p>
                        </div>
                        <DatePicker 
                            className='date-picker'
                            dateFormat="yyyy/MM/dd"
                            selected={dateOfVisit} 
                            onChange={(date) => 
                                setDateOfVisit(date)}
                            />
                    </div>

                    {/*  */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'></p>
                        </div>
                        <input 
                            className='input-1' 
                            value={name} 
                            onChange={(e) => 
                                setName(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={hospital}
                            sx={{marginLeft:5, width: 310, height: 23}}
                            onChange={(e) => {setHospital(e.target.value)}}
                        >
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                        </Select>
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={doctor} 
                            onChange={(e) => 
                                setDoctor(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={historyOfPresentIllness} 
                            onChange={(e) => 
                                setHistoryOfPresentIllness(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={pastMedicalHistory} 
                            onChange={(e) => 
                                setPastMedicalHistory(e.target.value)} 
                            />
                    </div>
    
                    {/*    */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'>  </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={medications} 
                            onChange={(e) => 
                                setMedications(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'> </p>
                        </div>
                        <input 
                            className='input-1' 
                            value={allergies} 
                            onChange={(e) => 
                                setAllergies(e.target.value)} 
                            />
                    </div>
    
                    {/*  */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'></p>
                        </div>
                        <input 
                            className='input-1' 
                            value={diagnosis} 
                            onChange={(e) => 
                                setDiagnosis(e.target.value)} 
                            />
                    </div>
    
                    {/*  */}
                    <div className='input-field row'>
                        <div className='desc-container-1'>
                            <p className='desc'></p>
                        </div>
                        <input 
                            className='input-1' 
                            value={treatment} 
                            onChange={(e) => 
                                setTreatment(e.target.value)} 
                            />
                    </div>
    
                    {/*   */}
                    <div className='input-field row'>
                        <div className='desc-container-2'>
                            <p className='desc'> </p>
                        </div>
                        <textarea 
                            className='input-2' 
                            value={additionalComments} 
                            onChange={(e) => 
                                setAdditionalComments(e.target.value)} 
                            />
                    </div>

                    <div className='row-center'>
                        <button className='submit-button' 
                                onClick={handleClick}>
                            
                        </button>
                    </div>
                </div>
            </div>
            }
            <Footer />
        </div>
    );
}    
                    
